.App {
    width: 100%;
    padding: 30px 0;
}

.myVideo {
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
    object-fit: cover;
}

.weather-card {
    width: 82%;
    max-width: 100%;
    margin: 0 auto;
}

.weather-card .title {
    font-size: 2.4vw;
    line-height: 2rem;
    text-align: center;
    color: #EFF5F5;
}

/* -------------FORM---------- */
.form-data {
    width: 450px;
    max-width: 100%;
    height: auto;
    margin: 40px auto 30px;
}

.form-data form {
    width: 100%;
    padding: 10px 20px;
    border-radius: 50px;
    background: #EFF5F5;
    display: flex;
    align-items: center;
    box-shadow: 6px 6px 10px #d6e4e573;
}

.form-data .input-group {
    display: block;
    position: relative;
    flex: 1;
}

.form-data .input-group label {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 10px;
    font-size: 18px;
    font-weight: bold;
    transition: all 0.4s ease;
    color: #605c5c;
    letter-spacing: 0.8px;
}

.form-data .input-group input {
    width: 100%;
    background: transparent;
    border: 0;
    outline: 0;
    padding: 0 10px;
    font-weight: 600;
    color: #102030;
    font-size: 18px;
}

.form-data .input-group input:focus~label,
.form-data .input-group input:valid~label {
    top: -6px;
    font-size: 10px;
}

.button {
    width: 30px;
    height: 30px;
    padding: 10px;
    background: #3C4048;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.button button {
    background: transparent;
    border: 0;
    outline: 0;
    cursor: pointer;
    color: #EFF5F5;
}

/* -------DETAILS CARD-------- */
.details {
    width: 830px;
    margin: 60px auto 0;
    background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.4)), url('https://images.pexels.com/photos/40465/pexels-photo-40465.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1');
    background-size: cover;
    background-position: center;
    border-radius: 20px;
    padding: 30px;
    color: #EFF5F5;
    position: relative;
    box-shadow: inset 0 0 20px #cfd2cf5e;
}

.right-side {
    position: absolute;
    right: 53px;
    top: 40px;
}

.right-side .city {
    font-size: 2vw;
    text-align: center;
}

.right-side .date {
    font-size: 0.8vw;
    text-align: center;
    line-height: 2rem;
}

.moreDetails {
    display: flex;
    margin-top: 50px;
    padding: 0 17px;
    align-items: center;
    justify-content: space-between;
}

.temp {
    margin-top: 20px;
    margin-left: 20px;
}

.temp .tempDeg {
    font-size: 3vw;
    margin-bottom: 25px;
}

.temp .desc {
    text-align: justify;
    margin-left: 10px;
    font-size: 1.2vw;
    font-weight: 200;
    letter-spacing: 1.1px;
}

.moreDetails>div {
    text-align: center;
    line-height: 3rem;
    color: #EFF5F5;
}

.moreDetails h2 {
    font-weight: 500;
    font-size: 1.1vw;
}

.moreDetails p {
    font-weight: bold;
    font-size: 1vw;
    text-shadow: 0 0 4px #eeefffaa;
}

.moreDetails .line {
    width: 1.8px;
    height: 6vh;
    background: #EFF5F5;
}

.loader {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 80px;
    height: 80px;
    border: 10px solid #EFF5F5;
    border-top: 10px solid #0A2647;
    border-radius: 50%;
    animation: rotate 2s linear infinite;
}

@keyframes rotate {
    from {
        transform: translate(-50%, -50%) rotate(0deg);
    }

    to {
        transform: translate(-50%, -50%) rotate(360deg);
    }
}

@media only screen and (max-width:800px) {
    .details {
        width: 100%;
    }

    .weather-card .title {
        font-size: 5.4vw;
    }

    .right-side {
        right: 28px;
    }

    .right-side .city {
        font-size: 6vw;
        margin-bottom: 8px;
    }

    .right-side .date {
        font-size: 2.3vw;
    }

    .temp {
        margin-top: 10px;
        margin-left: 6px;
    }

    .temp .tempDeg {
        font-size: 6vw;
        margin-bottom: 17px;
    }

    .temp .desc {
        font-size: 3.2vw;
        margin-left: 0;
    }

    .moreDetails {
        display: block;
        padding: 0;
    }

    .moreDetails div {
        margin: 8px 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .moreDetails h2 {
        font-size: 3.8vw;
        letter-spacing: 0.8px;
    }

    .moreDetails .line {
        display: none;
    }

    .moreDetails p {
        font-size: 3.8vw;
    }
}

@media only screen and (width:820px) {
    .details {
        width: 100%;
    }

    .weather-card .title {
        font-size: 4.2vw;
    }

    .details .right-side {
        right: 30px;
    }

    .right-side .city {
        font-size: 5vw;
        margin-bottom: 20px;
    }

    .right-side .date {
        font-size: 2vw;
    }

    .temp {
        margin-top: 10px;
        margin-left: 6px;
    }

    .temp .tempDeg {
        font-size: 6vw;
        margin-bottom: 17px;
    }

    .temp .desc {
        font-size: 2.8vw;
        margin-left: 0;
    }

    .moreDetails {
        display: block;
        padding: 0 10px;
    }

    .moreDetails div {
        letter-spacing: 2px;
        margin: 8px 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .moreDetails h2 {
        font-size: 2.6vw;
    }

    .moreDetails .line {
        display: none;
    }

    .moreDetails p {
        font-size: 2.4vw;
    }
}